import React from "react";
import "./style.css";
export default function Card({ name, img, type1, type2 }) {
  return (
    <>
      <div className="card">
        <h1>{name.toUpperCase()}</h1>
        <img className="sprite"src={img} alt={`Pokemon ${name}`} />
        <h2>
          Type 1:{type1} Type 2:{type2}
        </h2>
      </div>
    </>
  );
}
