import React from "react";
import { Link } from "react-router-dom";
import "./style.css";
export default function NavBar() {
  return (
    <div className="navBar">
      <p>Nav Bar</p>
    <Link to={"/"}>Exit pokedex </Link>
    <Link to={"/pokemons/create"}>Hatch new pokemon</Link>
    <Link to={"/pokemons/team"}>My team</Link>
    <Link to={""}>Dunno yet</Link>
    </div>
  );
}
